<template>
  <b-container class="mt-5">
    <b-row>
      <b-col lg="12">
        <div>
          <b-row>
            <b-col cols="11">
              <label class="mb-2 mt-3" for="inline-form-input-name">Api Key</label>
              <input type="text" v-model="id" class="form-control mx-0" rows="15" />
            </b-col>
            <b-col cols="1 mt-5">
              <button @click="loadInit" size="sm" class="btn btn-block btn-primary mb-3 px-3">Copy</button>
            </b-col>
          </b-row>
          <b-col cols="12 mb-3">
            <label class="mb-2 mt-3" for="inline-form-input-name">Init Method</label>
            <textarea disabled v-model="code" class="form-control mx-3" rows="3"></textarea>
          </b-col>
          <b-tabs content-class="mt-4" v-if="is_script">
            <b-tab title="Single Sign On">
              <SingleSignOn :api_key="api_key" />
            </b-tab>
          </b-tabs>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>
<script>
  import SingleSignOn from "./SingleSignOn";
  import DynamicJS from "../../js/core/dynamicjs";
  import copyToClipboard from "@/js/core/copy-to-clipboard";
  export default {
    components: { SingleSignOn },
    data() {
      return {
        is_script: false,
        code: "",
        api_key: "",
        id: "",
      };
    },
    mounted() {
      this.id = this.$route.params.key;
      this.code = `window._AppletIO.init(${this.$route.params.key})`;
      this.loadScript();
    },
    methods: {
      loadScript() {
        DynamicJS.downloadJS(this.getUrl());
        let vue = this;
        setTimeout(() => {
          vue.is_script = true;
          vue.loadInit();
        }, 1000);
      },
      loadInit() {
        this.code = `window._AppletIO.init(${this.id})`;
        window._AppletIO.init(this.id);
        let vue = this;
        setTimeout(() => {
          vue.api_key = vue.id;
        }, 1000);
        copyToClipboard.copy(this.id);
      },
      // Check script src url based on region
      getUrl() {
        let url = "https://appletio-js.500apps.com/";
        if (window.region == "dev" || window.region == "qa") return url + `${window.region}/main.js`;
        return url + "main.js";
      },
    },
  };
</script>
<style scoped></style>
