<template>
  <div class="">
    <b-row class="">
      <b-col cols="12">
        <div class="row">
          <div class="col-md-7">
            <label class="mb-2 mt-3" for="inline-form-input-name">Script</label>
            <b-card>
              <b-form>
                <textarea disabled :value="code" class="form-control mx-3" rows="15"></textarea>
              </b-form>
            </b-card>
          </div>
          <div class="col-md-5">
            <label class="mb-2 mt-3" for="inline-form-input-name">Single Sign On</label>
            <b-card>
              <div id="unified_SSO"></div>
            </b-card>
            <label class="mb-2 mt-3" for="inline-form-input-name">Result</label>
            <textarea disabled :value="profile_data" class="form-control mx-3" rows="8"></textarea>
          </div>
        </div>
      </b-col>
    </b-row>
  </div>
</template>
<script>
  export default {
    name: "SSO",
    data() {
      return {
        code: "",
        profile_data: "",
      };
    },
    props: {
      api_key: String,
    },
    mounted() {
      this.code = `window._AppletIO.getSSO().render('unified_SSO',(event) => {console.log(event);}))`;
    },
    methods: {},
    watch: {
      api_key() {
        window._AppletIO.getSSO().render("unified_SSO", (event) => {
          this.profile_data = JSON.stringify(event.data);
        });
      },
    },
  };
</script>
